h1, h2, h3, h4, p, ul {
  margin: 0;
  padding: 0;
}

.page {
  scroll-behavior: smooth;
  font-family: Inter, sans-serif;
}

.page__body {
  margin: 0;
}

.page__body--with--menu {
  overflow: hidden;
}

.page__menu {
  transition: transform .3s;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  transform: translateX(100%);
}

.page__menu:target {
  transform: translateX(0);
}

.page__section {
  margin-bottom: 120px;
}

.page__section-title {
  text-align: center;
  letter-spacing: -1.5px;
  color: #292929;
  margin-bottom: 48px;
  font-size: 32px;
  font-weight: bold;
  line-height: 32px;
}

@media (width >= 768px) {
  .page__section-title {
    margin-bottom: 64px;
    font-size: 40px;
    line-height: 40px;
  }
}

@media (width >= 1024px) {
  .page__section-title {
    margin-bottom: 64px;
    font-size: 48px;
    line-height: 48px;
  }
}

.container {
  max-width: 1260px;
  margin: 0 auto;
  padding: 0 20px;
}

@media (width >= 768px) {
  .container {
    padding: 0 72px;
  }
}

@media (width >= 1024px) {
