}

.icon {
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  width: 100%;
  padding-bottom: 100%;
  text-decoration: none;
  display: block;
  position: relative;
}

.icon__tooltip {
  letter-spacing: 2px;
  color: #292929;
  text-transform: uppercase;
  opacity: 0;
  pointer-events: none;
  width: max-content;
  padding-right: 12px;
  font-size: 12px;
  font-weight: 700;
  line-height: 16px;
  text-decoration: none;
  transition: opacity .3s;
  display: block;
  position: absolute;
  top: 50%;
  right: 100%;
  transform: translateY(-50%);
}

.icon:hover .icon__tooltip {
  opacity: 1;
  pointer-events: all;
}

.icon--phone {
  background-image: url("phone.f3db85bc.svg");
}

.icon--menu {
  background-image: url("menu.5cab4f73.svg");
